<template>
  <modal :show.sync="show" @close="close">
    <template slot="header">
      <h5 class="modal-title">{{ title }}</h5>
    </template>
    <div
      class="tk-popup-content"
      style="width: 100%; height: 100%; position: relative"
    >
      <loading size="80" v-if="isProfileDataLoading"></loading>
      <template v-else>
        <!-- basic info -->
        <div style="margin-top: 1.5rem">
          <div>
            <span>
              <div class="form-group">
                <label class="form-control-label">Full Name</label>
                <div class="has-label">
                  <input
                    type="text"
                    class="form-control form-control-muted tk-border-bottom tk-font-color-black1"
                    @keypress.enter.prevent=""
                    v-model="user.name"
                  />
                </div>
              </div>
            </span>
          </div>
          <div>
            <span>
              <div class="form-group">
                <label class="form-control-label">Email</label>
                <div class="has-label">
                  <input
                    type="email"
                    class="form-control form-control-muted tk-border-bottom tk-font-color-black1"
                    :class="{ 'is-invalid': isInvalid }"
                    @blur.prevent="validationEmail"
                    @keypress.enter.prevent=""
                    v-model="user.email"
                  />
                </div>
                <div
                  class="invalid-feedback"
                  style="display: block"
                  v-show="isInvalid"
                >
                  The email is not correct.
                </div>
              </div>
            </span>
          </div>
          <div>
            <span>
              <div class="form-group">
                <label class="form-control-label">Phone</label>
                <div class="has-label">
                  <div class="input-group">
                    <div class="input-group-prepend tk-px-width-px90">
                      <span
                        class="input-group-text tk-border-bottom tk-text-center"
                        style="padding: 0"
                      >
                        <!-- <span>+1</span> -->
                        <el-select
                          v-model="countryCodeSelected"
                          class="tk-select-border-none tk-border-bottom tk-select-bg-mute"
                        >
                          <el-option
                            v-for="(option, index) in countrySelects.options"
                            :key="index"
                            :label="option.label + '  ' + option.areaCode"
                            :value="option.value"
                          >
                          </el-option>
                        </el-select>
                      </span>
                    </div>
                    <input
                      type="text"
                      class="form-control form-control-muted tk-border-bottom tk-font-color-black1"
                      @keypress.enter.prevent=""
                      v-model="user.phone"
                    />
                  </div>
                </div>
              </div>
            </span>
          </div>
        </div>
      </template>
    </div>
    <template slot="footer">
      <base-button
        v-if="left !== ''"
        :type="leftType"
        @click.prevent="close"
        class="tk-font-color-lightGray tk-border-none"
        >{{ left }}</base-button
      >
      <base-button
        v-if="center !== '' && !isProfileDataLoading"
        :type="centerType"
        @click.prevent="onCenterTapped"
        class="tk-font-color-white tk-border-none"
        >{{ center }}</base-button
      >
      <!-- <base-button
        v-if="right !== ''"
        :type="rightType"
        @click.prevent="onRightTapped"
        class="tk-font-color-white tk-border-none"
        >{{ right }}</base-button
      > -->
      <button-with-loading
          v-if="!isProfileDataLoading"
          style="margin: 0 0 0 1rem"
          :text="right"
          @start="onRightTapped"
          :type="'main'"
          :complete="isButtonLoadingComplete"
        ></button-with-loading>
    </template>
  </modal>
</template>

<script>
import loading from "@/tkViews/components/layout/Loading.vue";
import DropzoneFileUpload from "@/tkViews/components/Inputs/DropzoneFileUpload.vue";
import { Select, Option } from "element-ui";
import commons from "@/util/commons";
import collections from "@/services/collections";
import query from "@/services/query";
export default {
  components: {
    loading,
    DropzoneFileUpload,
    [Select.name]: Select,
    [Option.name]: Option,
    buttonWithLoading: () => import("@/tkViews/components/layout/ButtonWithLoading.vue")
  },
  props: {
    title: String,
    left: {
      type: String,
      default: "",
    },
    center: {
      type: String,
      default: "",
    },
    right: {
      type: String,
      default: "",
    },
    leftType: {
      type: String,
      default: "secondary",
    },
    centerType: {
      type: String,
      default: "secondary",
    },
    rightType: {
      type: String,
      default: "main",
    },
    userId: {
      type: String,
      default: "",
    },
    id: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      show: false,

      isInvalid: false,
      countryCodeSelected: "+1",
      countrySelects: this.$tools.countrySelects,
      isProfileDataLoading: true,
      user: {
        userId: "",
        name: "",
        phone: "",
        email: "",
      },

      isButtonLoadingComplete: false
    };
  },
  watch: {
    countryCodeSelected(val, old) {
      let self = this;
      document.getElementsByClassName(
        "tk-select-border-none"
      )[0].childNodes[1].childNodes[1].style.display = "none";
      self.countrySelects.options.some((item) => {
        if (item.value === val) {
          setTimeout(() => {
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].value = item.areaCode;
            document.getElementsByClassName(
              "tk-select-border-none"
            )[0].childNodes[1].childNodes[1].style.display = "block";
          }, 10);
        }
      });
    },
  },
  async mounted() {
    let self = this;
    self.init();

    let user = await query.getUserInfoByUserId(self.userId)
    if (!self.$tools.isNull(user)) {
      self.user = user
    }

    self.isProfileDataLoading = false
  },
  methods: {
    close() {
      this.$emit("close");
      this.modalClose();
    },
    onCenterTapped() {
      this.$emit("onCenterTapped");
      this.modalClose();
    },
    async onRightTapped() {
      // this.$emit("onRightTapped");
      // this.modalClose();
      
      let self = this

      self.isButtonLoadingComplete = true
      await collections.user.doc(self.userId).update({
        name: self.user.name,
        phone: self.user.phone,
        email: self.user.email,
        updateTime: parseInt(new Date().getTime() / 1000).toString()
      }).then(async () => {
        await collections.teacherStudentList.doc(self.id).update({
          name: self.user.name,
          phone: self.user.phone,
          email: self.user.email,
          updateTime: parseInt(new Date().getTime() / 1000).toString()
        })
      }).then(() => {
        self.$notify({
          message: "Update Success!",
          type: "success",
        });
      })

      self.isButtonLoadingComplete = false
    },
    modalClose() {
      document.getElementsByTagName("body")[0].classList.remove("modal-open");
    },
    init() {
      let self = this;
      setTimeout(() => {
        self.show = true;
      }, 200);
    },
    validationEmail() {
      let self = this;

      if (self.$tools.isNull(self.user.email)) {
        self.isInvalid = false;
      } else {
        self.isInvalid = !self.$tools.isEmail(self.user.email);
      }
    },
  },
};
</script>

<style scoped>
.modal-body {
  padding-top: 0;
}
.form-group {
  margin-bottom: 1rem;
}
</style>